body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/** graph components **/
.header {
    margin-top: 0px;
    margin-bottom: 0px;
    text-align: left;
}

.sorry {
    font-size: 12px;
}

.key {
    margin-right: -300px;
    border: 4px solid orange;
}

.info {
    width: 15px;
    float: right;
    padding-left: auto;
    margin-right: 0px;
    margin-top: 5px;
}

.infoDiv {
    margin-right: 0;
    margin-left: auto;
    margin-top: 0px;
    margin-bottom: auto;
    height: 20px;
}

.info:hover + .hiddenInfo {
    display: block;
}

.heading {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.hiddenInfo {
    display: none;
    font-size: 12px;
    width: 12em;
    margin-top: 25px;
    margin-right: 0;
    text-align: start;
    border: 1px solid grey;
    padding: 5px;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5); 
    font-family: Helvetica, Arial;
    font-style: normal;
    font-weight: 100;
    border: 1px solid grey;
    border-radius: 0.5em;
    font-size: xx-small;
}

.infoHeading {
    margin-bottom: 2px;
}

@media only screen and (max-width: 400px) {
    .header {
        font-size: 16px;
    }

    .info {
        width: 10px;
    }

    .hiddenInfo {
        font-size: 8px;
        width: 30vw;
    }
}

@media only screen and (max-width: 280px) {
    .header {
        font-size: 14px;
    }

    .hiddenInfo {
        font-size: 8px;
    }
}
.songInfo {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    width: 180px;
}

.smallSongInfo {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    width: 100px;
}

.name { 
    margin: 5px;
    text-align: left;
    font-size: small;
    margin-bottom: 0;
    margin-right: 10px;
}

.artist { 
    margin: 5px;
    margin-top: 0;
    text-align: left;
    font-size: xx-small; 
    margin-right: 10px;
    margin-bottom: 0;
}
.genreComponent {
    font-size: xx-small;
    background-color: #000;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    color: #fff;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
    margin-top: 2px;
}

.parent { 
    display: -webkit-flex; 
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: left;
    margin-right: 10px;
    max-width: 180px;
    margin-bottom: 5px;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #000;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

html, body {
  height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  color: #eee;
  font-family: aktiv-grotesk, sans-serif;  
  font-size: 3vmin;
  font-style: normal;
  font-weight: 200;
}

.hidden {
  display: none;
}

/** Buttons **/

.btn {
  background-color: rgba(52, 51, 51, 0.5);   
  border-radius: 2em;
  border: 1px solid grey;
  color: white;
  font-size: 14px;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
  margin-top: 20px;
}

.btn:hover {
  background: #1DB954;
  /* background: linear-gradient(90deg, rgba(255,241,0,0.9) 0%, rgba(29,253,203,0.9) 50%, rgba(252,0,206,0.9) 100%); */
  border: none;
  color: #000;
}

.btn--login {
  margin: 0 auto;
}

/** Front Page **/

.logo {
  margin-top: 10px;
  width: 10%;
  height: 10%;
  padding-bottom: 130px;
}

.main {
  display: inline;
}

.hi {
  margin-top: 20px;
  font-size: 10px;
  color: grey;
}

@media only screen and (max-width: 500px) {
  .title {
    font-size: 40px;
  }

  .logo {
    margin-top: 10px;
    width: 20%;
    height: 20%;
  }
}

@media only screen and (max-width: 250px) {

  .btn {
    font-size: 10px;
  }

  .logo {
    margin-top: 10px;
    width: 25%;
    height: 25%;
  }

  .hi {
    width: 150px
  }
}
